.account {
    padding: 0 1rem;
    cursor: pointer;
  
    &__hint {
      font-size: var(--h6-size);
      margin-bottom: 0.2rem;
    }
  
    &__title {
      font-weight: bolder;
      margin-right: 0.2rem;
    }
  
    &__arrow {
      color: var(--hint-light-font-color);
      margin-bottom: -0.5rem;
    }
  }
  